import { Component, Prop, State, Event, EventEmitter, h, Host } from '@stencil/core';

export type CheckboxGroupItemType = 'checkbox' | 'radio' | 'icon-simple';
export type CheckboxGroupItemSize = 'sm' | 'md';

@Component({
  tag: 'bh-checkbox-group-item',
  styleUrl: 'bh-checkbox-group-item.css',
  shadow: true,
})
export class BhCheckboxGroupItem {
  /**
   * The type of input control
   */
  @Prop() type: CheckboxGroupItemType = 'checkbox';

  /**
   * The size variant
   */
  @Prop() size: CheckboxGroupItemSize = 'sm';

  /**
   * Unique value identifier for this item
   */
  @Prop() value: string = '';

  /**
   * Whether the item is checked (for checkbox type)
   */
  @Prop({ mutable: true }) checked: boolean = false;

  /**
   * Whether the item is selected (for radio type)
   */
  @Prop({ mutable: true }) selected: boolean = false;

  /**
   * Whether the item is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Material Symbol icon name (for icon-simple type)
   */
  @Prop() icon: string = '';


  /**
   * Title text
   */
  @Prop() itemTitle: string = '';

  /**
   * Subtitle text (inline with title)
   */
  @Prop() subtitle: string = '';

  /**
   * Description text
   */
  @Prop() description: string = '';

  /**
   * Radio group name (for radio type)
   */
  @Prop() name: string = '';

  @State() isHovered: boolean = false;
  @State() isFocused: boolean = false;

  /**
   * Emitted when the item state changes
   */
  @Event() bhItemChange!: EventEmitter<{ value: string; checked: boolean }>;

  private handleClick = () => {
    if (this.disabled) return;

    if (this.type === 'radio' || this.type === 'icon-simple') {
      if (!this.selected) {
        this.selected = true;
        this.bhItemChange.emit({ value: this.value, checked: true });
      }
    } else {
      this.checked = !this.checked;
      this.bhItemChange.emit({ value: this.value, checked: this.checked });
    }
  };

  private handleKeyDown = (event: KeyboardEvent) => {
    if (event.key === ' ' || event.key === 'Enter') {
      event.preventDefault();
      this.handleClick();
    }
  };

  private handleMouseEnter = () => {
    if (!this.disabled) this.isHovered = true;
  };

  private handleMouseLeave = () => {
    this.isHovered = false;
  };

  private handleFocus = () => {
    if (!this.disabled) this.isFocused = true;
  };

  private handleBlur = () => {
    this.isFocused = false;
  };

  private handleCheckboxChange = (event: CustomEvent<boolean>) => {
    event.stopPropagation();
    this.checked = event.detail;
    this.bhItemChange.emit({ value: this.value, checked: this.checked });
  };

  private handleRadioChange = (event: CustomEvent<string>) => {
    event.stopPropagation();
    this.selected = true;
    this.bhItemChange.emit({ value: this.value, checked: true });
  };

  render() {
    const isActive = (this.type === 'radio' || this.type === 'icon-simple') ? this.selected : this.checked;

    const containerClasses = {
      'checkbox-group-item': true,
      [`checkbox-group-item-${this.size}`]: true,
      [`checkbox-group-item-${this.type}`]: true,
      'checkbox-group-item-active': isActive,
      'checkbox-group-item-disabled': this.disabled,
      'checkbox-group-item-hovered': this.isHovered,
      'checkbox-group-item-focused': this.isFocused,
    };

    const renderInput = () => {
      if (this.type === 'radio') {
        return (
          <bh-radio-button
            size={this.size}
            selected={this.selected}
            disabled={this.disabled}
            value={this.value}
            name={this.name}
            onBhChange={this.handleRadioChange}
          />
        );
      }
      // For icon-simple, use `selected` state; for checkbox, use `checked` state
      const isChecked = this.type === 'icon-simple' ? this.selected : this.checked;
      return (
        <bh-checkbox
          size={this.size}
          checked={isChecked}
          disabled={this.disabled}
          onBhChange={this.handleCheckboxChange}
        />
      );
    };

    const renderIcon = () => {
      if (this.type === 'icon-simple' && this.icon) {
        return (
          <bh-featured-icon
            size="sm"
            icon={this.icon}
            color="gray"
            iconStyle="outlined"
          />
        );
      }
      return null;
    };

    const renderContent = () => (
      <div class="item-content">
        <div class="item-text-row">
          {this.itemTitle && <span class="item-title">{this.itemTitle}</span>}
          {this.subtitle && <span class="item-subtitle">{this.subtitle}</span>}
        </div>
        {this.description && <p class="item-description">{this.description}</p>}
      </div>
    );

    return (
      <Host>
        <div
          class={containerClasses}
          onClick={this.handleClick}
          onKeyDown={this.handleKeyDown}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          tabIndex={this.disabled ? -1 : 0}
          role={this.type === 'radio' ? 'radio' : 'checkbox'}
          aria-checked={String(isActive)}
          aria-disabled={this.disabled ? 'true' : null}
        >
          {this.type === 'icon-simple' ? (
            // Icon Simple: icon + content | input
            [renderIcon(), renderContent(), renderInput()]
          ) : (
            // Checkbox/Radio: input | content
            [renderInput(), renderContent()]
          )}
        </div>
      </Host>
    );
  }
}
